@import url('https://fonts.googleapis.com/css?family=Nunito:300,400,600,700,800,900&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Nunito', sans-serif;
  background-color: white;
}

.Container {
  background: white;
  width: 80%;
  max-width: 2000px;
  margin: auto;

  //Flex
  display: flex;
  flex-flow: row wrap;
  padding-bottom: 30px;
}
