@import url(https://fonts.googleapis.com/css?family=Nunito:300,400,600,700,800,900&display=swap);
*{box-sizing:border-box;margin:0;padding:0;font-family:'Nunito', sans-serif;background-color:white}.Container{background:white;width:80%;max-width:2000px;margin:auto;display:flex;flex-flow:row wrap;padding-bottom:30px}

.Home{display:flex;flex-wrap:wrap;justify-content:space-between}

.Card{width:330px;height:500px;margin:30px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);border-radius:16px;display:flex;flex-direction:column}.Card__img{height:230px;border-radius:16px 16px 0px 0px}.Card__text{height:230px;border-radius:0px 0px 16px 16px;padding:20px}.Card__text--title{font-size:20px;font-weight:bold}.Card__text--description{font-weight:ligth;font-size:16px}.Card__link{text-align:right;align-self:flex-end;margin-right:20px}.Card:hover{box-shadow:0 8px 16px 0 rgba(0,0,0,0.2)}

.Navbar{width:100%;padding-top:30px;font-size:24px;padding-bottom:40px;display:flex;justify-content:space-between;align-items:center;flex-flow:row wrap}.Navbar__nav{width:50%;display:flex;flex-wrap:wrap;justify-content:flex-end}.Navbar__link{color:black;font-weight:bold;text-align:center;padding:10px;outline:none}.Navbar__link:hover{text-decoration:none}.Navbar__link--element{padding:10px 30px}

.texts{font-size:15px;margin-top:10px;background-color:rgba 255,255,255,0.849;color:#000000;padding:1%;height:400px}.p-align{text-align:left}.monaco-theme{border:gray 1px solid}.font-size{font-size:10%}.compiler{width:100%;display:flex;flex-direction:column;align-items:flex-start;flex-wrap:nowrap}.compiler--left{align-items:flex-end}.compiler__editor{text-align:left;width:100%}.compiler__output{margin-top:20px;width:100%;height:100px}.compiler__button{margin-top:10px;padding:10px;border-radius:10px;border:none;background:#275aac;color:#ffffff;font-weight:bold;font-size:18px;align-items:flex-end}

.Compiler{text-align:center;width:100%;padding-right:30px}.Compiler__title{font-size:36px;font-weight:bolder}

.text-area{resize:none;font-size:15px;margin-top:10px;background-color:rgba(255,255,255,0.849);color:#000000;padding:1%;width:600px;height:80px}.p-align{text-align:left}.monaco-theme{border:gray 1px solid}.font-size{font-size:10%}

.practice-container {
  width: 100%;
  padding: 10px 60px;
  background-color: #ffffff;
}

.code-editor {
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(82, 82, 82);
}

.editor-container {
  padding: 10px; 
}

.tab-sizes{
  font-size: 15px;
}

.editor-container > div {
  float: left;
  margin: 50px;
  margin-bottom: 5px;
}

.information {
  text-align: center;
}

.description {
  text-align: justify;
  display: block;
}

.text-area{
  resize: none;
  font-size: 15px;
  margin-top: 10px;
  background-color:rgba(255, 255, 255, 0.849);
  color: #000000;
  padding: 1%;
  width: 600px;
  height: 80px;
}

.radio-container{
  padding: 5px;
}

.radio-container > div{
  margin-left: 170px;
  margin-bottom: 20px;
}

.monaco-theme{
  border: gray 1px solid;
}


.info{background-color:white;padding-top:20px;width:100%;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around}.info__text{width:605px;text-align:justify;font-weight:24}.info__text--title{font-weight:bold}.info__text--description{padding-top:25px;font-weight:18}.info .btn{margin-top:30px;background-color:#3e5ba9;color:white;width:141px;height:41px;border-radius:30px;border:none}.info__image{text-align:end;width:50%}

.Module{text-align:center;width:100%;padding-right:30px;margin-bottom:60px}.Module__title{font-weight:bolder}

.tab__button{font-size:20px;font-weight:bold;margin-right:40px}.tab__button:hover{cursor:pointer;color:#275aac}.tab__button--active{color:#275aac;font-weight:bolder}

.tabs--row{display:flex;flex-direction:row;align-items:center}.tabs--column{width:20%;display:flex;flex-direction:column;justify-content:space-between;height:600px;align-items:flex-start}

.ejemplo{margin-top:40px;display:flex;flex-direction:row;flex-wrap:nowrap;align-items:flex-end;justify-content:space-between}.ejemplo__editor{width:45%}.compiler__button:hover{color:white;text-decoration:none}

.Compare{display:flex;flex-direction:row;justify-content:space-between;align-items:flex-start;flex-wrap:nowrap;width:100%}.Compare textarea{align-self:end}.Compare__text-area{display:flex;flex-direction:column;justify-content:space-between;align-items:center}.texts{font-size:15px;background-color:rgba 255,255,255,0.849;color:#000000;height:110px;width:500px;margin:0px}.texts2{font-size:15px;background-color:rgba 255,255,255,0.849;color:#000000;height:290px;width:250px;margin:0px}.outline{border:#888 1px solid}.compiler__button{padding-bottom:10px}

.Practice{text-align:center;width:100%;padding-right:30px;margin-bottom:60px}.Practice__title{font-weight:bolder}.Practice__tabs{display:flex;flex-direction:column;align-items:flex-start}.Practice__content{display:flex;flex-direction:column;justify-content:flex-start}.Practice__text{text-align:left;padding:20px 0px}.exercicies{width:650px;height:500px;text-align:justify}

